//BASE STYLE
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%; // 10/16px = 62.5% -> 1rem = 10px
}

body {
  font-family: $font-primary;
  color: $text-color;
  font-weight: 400;
  background-image: linear-gradient($soft-violet, $soft-blue);
  background-repeat: no-repeat;
  min-height: calc(100vh);
}
