@import url(https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,*::before,*::after{margin:0;padding:0;box-sizing:inherit}html{box-sizing:border-box;font-size:62.5%}body{font-family:"Kumbh Sans",sans-serif;color:#000;font-weight:400;background-image:-webkit-gradient(linear, left top, left bottom, from(#af67e9), to(#6565e7));background-image:linear-gradient(#af67e9, #6565e7);background-repeat:no-repeat;min-height:calc(100vh)}.continer{margin:0vh auto 10vh auto;padding-top:14rem;max-width:440px;position:relative}.card-box{display:none}.accordion-card{padding:13rem 0rem 2rem 0rem;position:relative;background-image:url(/FM-accordion-card/static/media/bg-pattern-mobile.55aee912.svg);background-repeat:no-repeat;background-position:top center;background-color:#fff;border-radius:2rem;box-shadow:#26394d 0px 21px 37px -10px;min-height:58rem}.card-img-container{position:absolute;top:-11rem;left:0;right:0;margin-left:auto;margin-right:auto;width:28rem}.accordion-card-title{font-size:3.2rem;text-align:center;padding-bottom:2rem}.card-list{display:flex;flex-direction:column;padding:1.5rem 1rem 3rem 1rem}.card-list .card-list-item{list-style-type:none;margin:0.6rem 2rem;border-bottom:1px solid #e7e7e9}.card-list .card-list-item .card-item-title{font-size:1.4rem;margin:0.2rem 0rem 1rem 0rem;padding:0rem;display:flex;justify-content:space-between;align-items:center;-webkit-transition:color 0.3s;transition:color 0.3s}.card-list .card-list-item .card-item-title .arrow-icon{color:#f47c57;font-size:1.5rem;margin-bottom:1rem}.card-list .card-list-item .card-item-title:hover{cursor:pointer;color:#f47c57}.card-list .card-list-item .card-item-description{font-size:1.2rem;font-weight:400;padding:0rem 3rem 1.5rem 0rem;line-height:2rem;display:none;-webkit-animation:fadeIn 1s ease-in both;animation:fadeIn 1s ease-in both}.card-list .card-list-item.active{font-weight:700}.card-list .card-list-item.active .card-item-description{display:block}@media (min-width: 1060px){.continer{margin:0vh auto 10vh auto;padding-top:14rem;max-width:960px;position:relative}.card-img-container{position:relative;width:30rem;top:-10px;left:-173px}.card-box{display:block;position:absolute;width:18rem;top:39rem;left:-8.7rem;z-index:2;-webkit-transition:left 0.8s;transition:left 0.8s}.card-box.active{left:-12.5rem}.accordion-card{padding:7rem 0rem 5rem 0rem;background-image:url(/FM-accordion-card/static/media/bg-pattern-desktop.f51e07a2.svg);background-repeat:no-repeat;background-position:-56rem -29rem;background-size:cover;display:grid;grid-template-columns:1fr -webkit-min-content;grid-template-columns:1fr min-content;align-items:center;overflow:hidden}.accordion-card-title{text-align:left;padding-left:2rem}.card-list{padding:0rem 9rem 3rem 0rem;max-height:38rem;min-width:48rem}.card-list .card-list-item .card-item-title{padding:0rem 1rem 0rem 0rem}}@-webkit-keyframes fadeIn{from{opacity:0.5;-webkit-transform:translate3d(0, -5%, 0);transform:translate3d(0, -5%, 0)}to{opacity:1;-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}}@keyframes fadeIn{from{opacity:0.5;-webkit-transform:translate3d(0, -5%, 0);transform:translate3d(0, -5%, 0)}to{opacity:1;-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}}

